<template>
  <div>
    <div class="header">
			<div class="header-inner clearfix">
				<div class="logo-box">
					<a href="/index.html"><img src="/static/images/logo_1.png" alt="" /></a>
				</div>				
				<div class="nav-bar">
					<ul class="clearfix">
						<li v-for="(item,index) in vavBarList" :key="index"><a :href="item.url" :class="index==navBarIndex?'active':''">{{item.title}}</a></li>
					</ul>
				</div>
				<div class="nav-bar-btn"></div>
			</div>
		</div>
  </div>
</template>

<script>
export default {
	name: 'Header',
	data() {
		return {
			navBarIndex:'',
			vavBarList:[
				{url:'/index.html',title:'首页'},
				{url:'/introduce.html',title:'介绍'},
				{url:'/information.html',title:'资讯'},
				{url:'/technology.html',title:'技术'},
				{url:'/event.html',title:'活动'},
				{url:'/college.html',title:'学院'},
				{url:'/team.html',title:'团队'},
			]
		}
	},
  mounted() {
		this.navBarIndex =this.$parent.navBarIndex

    $('.header .header-inner .nav-bar-btn').click(function(e){
			e.stopPropagation();
			if($('.nav-bar-btn').css('display')=='block'){
				if($('.nav-bar').hasClass('on')){
					$('.nav-bar').removeClass('on');
					$('.nav-bar').animate({
						left:-300+"px"
					},500,"easeInQuad");
				}else{
					$('.nav-bar').addClass('on');
					$('.nav-bar').animate({
						left:0+"px"
					},500,"easeInQuad");
				}
			}
		});
		$(document).click(function(){
			$('.nav-bar').removeClass('on');
			$('.nav-bar').animate({
				left:-300+"px"
			},500,"easeInQuad");
    })
    $(window).resize(function(){
      	if($('.nav-bar-btn').css('display')=='none'){
          $('.nav-bar').removeClass('on');
          $('.nav-bar').attr('style','')
        }
    })
  },
}
</script>



